function _App(){"use strict";}

  _App.prototype.render=function() {"use strict";
    return (
      React.DOM.div({className: "table-container"}, 
        CardCtrl({ctx: new Model(), body: TableCtrl}), 
        CardCtrl({ctx: new Model(), body: TableCtrl}), 
        CardCtrl({ctx: new Model(), body: TableCtrl}), 
        CardCtrl({ctx: new Model(), body: TableCtrl})
      )
    );
  };


export var App = React.createClass(_App.prototype);

var renderApp = function() 
  {return React.renderComponent(App(null), document.getElementById('react-app'));};

renderApp();

